        this.$message({
          type: 'warning',
          message: '此类问题回答完毕，请选择其他类问题！'
        })
        return
      }
      let index = Math.floor(Math.random() * length)
      this.showQuestion = this.questionList[index]
      this.questionList.splice(index, 1)
      console.log(this.backupsData)
    },
